.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  picture {
    display: block;
    width: 100%;
    height: 100%;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;
  transform-origin: center;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }

  .form__field {
    opacity: 0;
    transform: translateX(100%);
    transform-origin: 100%;

    &::placeholder {
      opacity: 0;
      transition: opacity 0.3s 0.7s;
    }
  }
}

.result--prize {
  .prize-image {
    top: 45%;
    left: 50%;
    transform: translateX(-50%);
    width: 820px;
    height: 480px;

    img {
      display: block;
      position: absolute;
      object-fit: cover;
      height: auto;
    }

    &__suitcase {
      top: -10%;
      left: 14%;
      width: 60%;
      transform-origin: bottom center;
    }

    &__flamingo {
      top: 18%;
      left: 10%;
      width: 45%;
      transform-origin: bottom right;
    }

    &__leaf-1 {
      top: -12%;
      left: 50%;
      width: 45%;
      transform-origin: bottom left;
    }

    &__leaf-2 {
      top: 25%;
      left: -5%;
      width: 45%;
      transform-origin: top right;
    }

    &__leaf-green {
      top: 30%;
      left: 48%;
      width: 55%;
      transform-origin: bottom left;
    }

    @media (max-width: $mobile) {
      width: 100vw;
      height: 60vw;
    }
  }
}

.result--negative {
  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }

  .result__button {
    opacity: 0;

    span {
      transform: translateX(24px) rotate(90deg);
    }
  }
}

.screen--result.screen--show {
  .result--trip,
  .result--prize {
    .form__button {
      animation: fadeIn $duration-long 0.3s both, textAppeared $duration-long 0.5s both;
    }

    .form__field {
      animation: fadeIn 0.2s 0.3s both, slideInRight $duration-extra-long 0.3s both;

      &::placeholder {
        opacity: 1;
      }
    }
  }

  .result--prize {
    .prize-image {
      &__suitcase {
        animation: zoomIn $duration-extra-long both;
      }

      &__flamingo {
        animation: prize__flamingo-show $duration-extra-long both, prize__flamingo-fly 3s alternate $duration-extra-long infinite;
      }

      &__leaf-1 {
        animation: prize__show-leaf-1 1s ease $duration-medium both;
      }

      &__leaf-2 {
        animation: prize__show-leaf-2 1s ease-out $duration-medium both;
      }

      &__leaf-green {
        animation: prize__show-leaf-1 1s ease-out $duration-long both;
      }
    }
  }

  .result--negative {
    .result__button {
      animation: fadeIn $duration-medium 0.5s both;

      span {
        animation: rollInRight $duration-medium 0.5s both;
      }
    }
  }
}
